.App {

}

.search {
  padding: 1em;
}

.search input {
  line-height: 3em;
  font-size: 2em;
  width: 100%;
  margin: 0;
  background: transparent;
  border: none;
  border-bottom: 2px solid rgba(255,255,255,0.6);
  color: #fff;
  outline: none;
}

.grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
  grid-auto-rows: 20px;
}

.notfound {
  font-size: 2em;
  line-height: 3em;
  color: #fff;
  text-align: center;
}

.coins {
  list-style: none;
  -ms-flex-pack: center;
      justify-content: center;
  color: #fff;
  padding: 0;
  margin: 0;
}

.coin {
  width: 300px;
  text-align: center;
  cursor: pointer;
  text-align: center;
  line-height: 1em;
  padding-bottom: 1em;
  font-size: 1em;
  margin: 1em;
  border: 1px solid rgba(0,0,0,0.8);
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,1);
          box-shadow: inset 0 0 6px rgba(0,0,0,1);
  border-radius: 10px;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  word-wrap: break-word;
  color: #fff;
  background: -webkit-gradient(linear, left bottom, left top, color-stop(1.4em, #222), color-stop(3em, #333), to(#444));
  background: -webkit-linear-gradient(bottom, #222 1.4em, #333 3em, #444 100%);
  background: -o-linear-gradient(bottom, #222 1.4em, #333 3em, #444 100%);
  background: linear-gradient(to top, #222 1.4em, #333 3em, #444 100%);
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  overflow: hidden;
  outline: none;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  /* animation: growing ease-in-out 2s infinite; */
}

.coin:hover, .coin:active, .coin:focus {
  /* animation: growing ease-in-out 2s infinite; */
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.coin .title {
  margin-top: 1em;
  font-weight: bold;
}

img {
  max-width: 100%;
  height: 200px;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.display {
  color: #fff;
}

.ticker {
  font-size: 1.2em;
  text-align: center;
}

.red {
  color: red;
  font-weight: bold;
  -webkit-transition: color 0.3s ease-in;
  -o-transition: color 0.3s ease-in;
  transition: color 0.3s ease-in;
}

.green {
  color: green;
  font-weight: bold;
  -webkit-transition: color 0.3s ease-in;
  -o-transition: color 0.3s ease-in;
  transition: color 0.3s ease-in;
}

@-webkit-keyframes growing {
  from {
    -webkit-transform: scale(1);
            transform: scale(1)
  }
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05)
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1)
  }
}

@keyframes growing {
  from {
    -webkit-transform: scale(1);
            transform: scale(1)
  }
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05)
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1)
  }
}



